import React, {useContext, useEffect, useState} from 'react';
import {ItemsContext} from '../../infrastructure/ui/ItemsContext';
import {useParams} from 'react-router-dom';
import {ItemView} from '../components/Item';
import {Card, CardContent, Grid} from '@mui/material';
import {Item} from '../../domain/entities/item';

// I want to add a property to the Room component that is the name of the room
interface RoomProps {
  name: string;
}

export const RoomWrapper = () => {
  const {room} = useParams<{ room: string }>();
  return (
    <Room name={room || ''}/>
  );
}

const Room = ({name}: RoomProps) => {
  const {items, categories} = useContext(ItemsContext);

  const [roomItems, setRoomItems] = useState(items.filter((item: { room: string; }) => item.room === name));

  useEffect(() => {
    setRoomItems(items.filter((item: { room: string; }) => item.room === name));
  }, [name, categories, items]);

  return (
    <div>
      <Card
        title={name}
        sx={{
          margin: 'auto',
          alignItems: "center",
          justify: "center",
          maxWidth: '80%',
          maxHeight: 'inherit',
          padding: 4,

        }}
      >
        <CardContent>
          <Grid container spacing={4}>
            {roomItems.sort((a: { name: string; }, b: { name: any; }) => a.name.localeCompare(b.name)).map((item: Item, index: React.Key | null | undefined) => (
              <Grid item xs={12} sm={6} md={4} key={index}>
                <ItemView key={item.category + item.room + item.name} item={item}/>
              </Grid>
            ))}
          </Grid>

        </CardContent>
      </Card>
    </div>
  );
};

export default Room;
