import React, {useContext, useState} from "react";
import {PsychologyOutlined} from "@mui/icons-material";
import ShareIcon from '@mui/icons-material/Share';

import {
  Box,
  Button,
  Card,
  CardActionArea,
  Container,
  LinearProgress,
  MenuItem,
  Select,
  SelectChangeEvent,
  TextField,
  Typography,
} from "@mui/material";
import {brainInstance} from '../../infrastructure/dataAccess/https';
import {AuthContext} from '../../infrastructure/ui/AuthContext';


class Botonality {
  id: string;
  name: string;
  system_description: string;
  prompt_exemple: string;

  constructor(id: string, name: string, system_description: string, prompt_exemple: string) {
    this.id = id;
    this.name = name;
    this.system_description = system_description;
    this.prompt_exemple = prompt_exemple;
  }

  async process(input: Message[]): Promise<Message> {
    const system: Message[] = [
      {
        role: "system",
        content: this.system_description
      }
    ];
    const payload = [...system, ...input];
    const hubResponse = await brainInstance.post('/bot/talk', payload)
    return hubResponse.data as Message;
  }

}


interface ChatbotProps {
}

interface Message {
  role: string;
  content: string;
}

const availableBotonality: Botonality[] = [
  new Botonality("0", "✨ ChatGPT", "", "écrire ici"),
  new Botonality("1", "👨‍💻 devWithMe", "Voici une conversation entre une application " +
    "écrite en React et son developpeur. ",
    "Je veux développer un button en React, using mui"),
  new Botonality("2", "👴 Socrates",
    "Voici une conversation avec Socrates, le philosophe grec. " +
    "Il reponds de facon curiuese et interessante.",
    "Dis Socrates, qu'est ce que la vie ?"),
  new Botonality("3", "🧞 Azonto Aldini",
    "Azonto Aldini est un geni qui transforme les envies en idées. Il pose de questions et recadre son" +
    "interlocuteur pour arriver à mettre en place ses idées",
    "J'ai pensé a faire un chatbot qui fait des blagues, qu'en penses tu ?"),

  new Botonality("4", "🧑‍🍳 Ferran AdrIA",
    " Ferran AdrIA est un cussinier spécialisé dans la gastronomie méditerranéene.",
    "Je veux impréssioner mes colègues, t'as une idée de recette ?")
];

const Chatbot: React.FC<ChatbotProps> = () => {
  const { userName } = useContext(AuthContext);
  const [chatHistory, setChatHistory] = useState<Message[]>([]);
  const [inputValue, setInputValue] = useState<string>("");
  const [selectedModel, setSelectedModel] = useState<Botonality>(availableBotonality[0]);
  const [conversationDepth, setConversationDepth] = useState<number>(8);
  const [isLoading, setIsLoading] = useState<boolean>(false);

  const handleInputChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setInputValue(event.target.value);
  };

  const handleModelChange = (event: SelectChangeEvent) => {
    const modelId = event.target.value as string;
    const model = availableBotonality.find((m) => m.id === modelId);
    setSelectedModel(model || availableBotonality[0]);
  };

  const handleCopyAllConversation = () => {
    const conversation = chatHistory.map((m) => `${m.role}: ${m.content}`).join("\n---\n");
    navigator.clipboard.writeText(conversation);

  }
  const handleUseModelClick = async () => {
    if (!selectedModel) {
      return;
    }
    setIsLoading(true);
    try {


      const chatToModel = [...chatHistory, {role: "user", content: inputValue}]
      setChatHistory(chatToModel);
      const result = await selectedModel.process(chatToModel);

      setChatHistory([...chatToModel, result].slice(-conversationDepth));
      setIsLoading(false);

      setInputValue("");
    } catch (error) {
      console.error(error);
      setIsLoading(false);
    }
  };

  return (
    <Container>
      <Box display="grid" flexDirection="column" alignItems="center">
        <Typography variant="h6" sx={{fontWeight: "light"}}>
          Botonality:
        </Typography>
        <Select
          label="botonality"
          value={selectedModel?.id || ""}
          onChange={handleModelChange}
          disabled={isLoading}
        >
          {availableBotonality.map((model) => (
            <MenuItem key={model.id} value={model.id}>
              {model.name}
            </MenuItem>
          ))}
        </Select>
      </Box>

      {
        chatHistory.map((message, index) => (
          <CardActionArea onClick={() => {
            navigator.clipboard.writeText(message.content)
          }}
          >
            <Card sx={{p: 2, m: 1, bgcolor: message.role === "user" ? "white" : "rgba(0,252,191,0.2)"}}>

              <Box display="grid" flexDirection="column" alignItems="center">
                <Typography key={index} gutterBottom color="text.secondary">
                  {message.role === "user" ? userName.split(' ')[0]  : selectedModel.name}
                </Typography>
                <Typography key={index} variant="body1" sx={{ whiteSpace: 'pre-line'}}>
                  {message.content}
                </Typography>
              </Box>
            </Card>
          </CardActionArea>

        ))
      }

      <Box display="grid" flexDirection="column" alignItems="center">
        <TextField
          value={inputValue}
          multiline
          rows={chatHistory.length == 0 ? 8 : 2}
          variant="filled"
          placeholder={chatHistory.length == 0 ? selectedModel.prompt_exemple : ''}
          onChange={handleInputChange}
          disabled={isLoading}
        />
      </Box>


      {isLoading && (
        <div>
          <Box display="flex" flexDirection="column" alignItems="center">
            <Box sx={{width: '100%'}}>
              <LinearProgress/>
            </Box>
          </Box>
        </div>
      )}

      <Box display="flex" flexDirection="row" alignItems="center" justifyContent={'center'} m={1}>
        <Button
          variant="contained"
          color="primary"
          onClick={handleUseModelClick}
          disabled={!selectedModel || isLoading}
        >
          <PsychologyOutlined/>
          Send
        </Button>

        <Button
          variant="contained"
          color="primary"
          onClick={handleCopyAllConversation}
          disabled={!selectedModel || isLoading}
        >
          <ShareIcon/>
          Copy
        </Button>
      </Box>


    </Container>
  );
};

export default Chatbot;
