import React, {useContext} from 'react';
import TopBar from '../components/TopBar';
import {RoomWrapper} from './Room';
import {useParams} from 'react-router-dom';
import {CategoryBarWrapper} from '../components/CategoryBar';
import {ItemsContext} from "../../infrastructure/ui/ItemsContext";
import {AuthContext} from "../../infrastructure/ui/AuthContext";
import {Backdrop, CircularProgress} from '@mui/material';

const Home = () => {
  // get the name from the url
  let {category, room} = useParams<{ category: string, room: string }>();
  const {items} = useContext(ItemsContext);
  const {userName} = useContext(AuthContext);
  return (
    <div>
      <TopBar title={''}/>
      {items.length === 0 &&
        <Backdrop
          sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
          open={items.length === 0}
        >
          Loading {userName}...
          <CircularProgress color="inherit" />
        </Backdrop>
      }
      {category &&
        <CategoryBarWrapper/>
      }
      {room &&
        <RoomWrapper/>
      }

    </div>
  );
};

export default Home;
