import {houseKeepingInstance} from '../../infrastructure/dataAccess/https';
import {Item} from '../entities/item';
import {Room} from '../entities/room';
import {Category} from "../../infrastructure/ui/ItemsContext";

export interface ItemsRepository {
  getAll(): Promise<Item[]>;

  getRooms(): Room[];

  getCategories(): Category[];

  save(items: Item[]): Promise<boolean>;
}

class ItemsRepositoryImpl implements ItemsRepository {
  items: Item[];
  rooms: Room[];
  categories: Category[];

  constructor() {
    this.items = [];
    this.rooms = [];
    this.categories = [];
  }

  async getAll(): Promise<Item[]> {
    const response = await houseKeepingInstance.get<Item[]>("/items");

    this.items = response.data;
    this.rooms = this.getRooms();
    this.categories = this.getCategories();
    return this.items;
  }

  async save(items: Item[]): Promise<boolean> {
    await houseKeepingInstance.post("/items", items);
    return true
  }

  getCategories(): Category[] {
    const roomIcon = [...new Set(this.items.map(item => item.category + '$' + item.icon_category))];
    return roomIcon.map(roomIcon => {
      return {
        name: roomIcon.split('$')[0],
        icon: roomIcon.split('$')[1],
      }
    })
  }

  getRooms(): Room[] {

    const roomIcon = [...new Set(this.items.map(item => item.category + '$' + item.room + "$" + item.icon_room + "$" + item.icon_category))];

    return roomIcon.map(roomIcon => {
      return {
        name: roomIcon.split('$')[1],
        icon: roomIcon.split('$')[2],
        category: roomIcon.split('$')[0],
        icon_category: roomIcon.split('$')[3],
      }
    })
  }
}

export const itemsRepository: ItemsRepository = new ItemsRepositoryImpl();
