import React, {createContext, ReactNode, useEffect, useState} from "react";
import {Item} from '../../domain/entities/item';
import {itemsRepository} from "../../domain/repositories/ItemRepository";
import {Room} from '../../domain/entities/room';

export interface Category {
  name: string;
  icon: string;
}

interface ItemsContextType {
  items: Item[];
  rooms: Room[];
  categories: Category[];
  updateItem: (item: Item) => void;
  saveRepository: (items: Item[]) => Promise<boolean>;
}

export const ItemsContext = createContext<ItemsContextType>({
  items: [],
  rooms: [],
  categories: [],
  updateItem: (item: Item) => {
  },
  saveRepository: (items: Item[]) => {
    return new Promise<boolean>((resolve) => resolve(false));
  },
});

interface ItemsProviderProps {
  children: ReactNode;
}

export const ItemsProvider: React.FC<ItemsProviderProps> = ({children}) => {
  const [items, setItems] = useState<Item[]>([]);
  const [rooms, setRooms] = useState<Room[]>([]);
  const [categories, setCategories] = useState<Category[]>([]);

  useEffect(() => {
    const fetchData = async () => {
      const items = await itemsRepository.getAll();
      const rooms = itemsRepository.getRooms();
      const categories = itemsRepository.getCategories();
      setItems(items);
      setRooms(rooms);
      setCategories(categories);
    };

    fetchData().then(() => console.log('items fetched'))
  }, []);

  const saveRepository = async (items: Item[]): Promise<boolean> => {
    return await itemsRepository.save(items);
  }

  const updateItem = (item: Item) => {
    const newItems = items.map((i) => {
      if (i.name === item.name && i.room === item.room && i.category === item.category) {
        return item;
      }
      return i;
    });
    setItems(newItems);
  }

  return <ItemsContext.Provider
    value={{items, rooms, categories, updateItem, saveRepository}}>{children}</ItemsContext.Provider>;

};
