import React from 'react';
import {BrowserRouter as Router, Route, Routes} from 'react-router-dom';
import Home from './presentation/containers/Home';
import Login from './presentation/components/Login';
import About from './infrastructure/ui/About';
import {RoomWrapper} from './presentation/containers/Room';

import {ItemsProvider} from './infrastructure/ui/ItemsContext';
import {AuthContext} from './infrastructure/ui/AuthContext';
import BotHub from './presentation/components/BotHub';
import {CategoryBarWrapper} from './presentation/components/CategoryBar';
import {BrainInterfaceWrapper} from './presentation/containers/BrainInterface';

const App = () => {
  const renderRoutes = (isAuthenticated: boolean) => {
    if (!isAuthenticated) {
      return (
        <Routes>
          <Route path="/*" element={<Login/>}/>
        </Routes>
      )
    }
    return (
      <ItemsProvider>
        <Routes>
          <Route path="/" element={<Home/>}/>
          <Route path="/bot" element={<BotHub/>}>
            <Route path="/bot/:brain/" element={<BrainInterfaceWrapper/>} />

          </Route>

          <Route path="/home" element={<Home/>}>
            <Route path="/home/:category/" element={<CategoryBarWrapper/>}>
              <Route path="/home/:category/:room" element={<RoomWrapper/>}/>
            </Route>

          </Route>

        </Routes>
      </ItemsProvider>
    );

  }

  return (
      <Router>
        <AuthContext.Consumer>
          {({isAuthenticated}) => renderRoutes(isAuthenticated)}
        </AuthContext.Consumer>
      </Router>
  );
};

export default App;

