import React, {useContext, useState} from 'react';
import Slider from '@mui/material/Slider';
import {Item} from '../../domain/entities/item';
import {ItemsContext} from "../../infrastructure/ui/ItemsContext";
import {Box, Button, Card, CardContent, Input, Typography} from "@mui/material";
import DeleteButton from "./DeleteItemButton";
import {Create} from "@mui/icons-material";


export const ItemView = ({item}: { item: Item }) => {
  const {updateItem} = useContext(ItemsContext);
  const [quantity, setQuantity] = useState(item.quantity);
  const [step, setStep] = useState(1);
  const [max, setMax] = useState(item.max);
  const [isEditing, setIsEditing] = useState(false);

  const onChangeItem = (event: Event, value: number | number[]) => {
    setQuantity(value as number);
    const newItem: Item = {...item, quantity: value as number};
    updateItem(newItem);
  };

  const onClickEdit = () => {
    setIsEditing(!isEditing);
  }

  const onEditMax = (event: HTMLInputElement) => {
    console.log(event)
  }

  const getRef = (item: Item) => {
    return (item.permanent ? `https://es.wallapop.com/app/search?keywords=` : `https://tienda.mercadona.es/search-results?query=`) + item.name;
  }

  return (
    <div>
      <Card>

        <CardContent>
          <Typography component="div" variant="h6">
            <a href={getRef(item)}
               target="_blank"
               rel="noreferrer"> {item.name}  </a>
          </Typography>
          <Typography variant="subtitle1" color="text.secondary" component="div">
            {quantity} / {
            isEditing ?
              <Input value={max} onChange={(event) => setMax(Number(event.target.value))}/>
              :
              max
          }
            {
              item.unit
            }
          </Typography>


          <Box sx={{display: 'flex', alignItems: 'center', pl: 1, pb: 1}}>

            <Slider
              value={quantity}
              aria-label="Default"
              min={0}
              step={step}
              max={max}
              onChange={onChangeItem}
            />
            {
              isEditing &&
              <Input
                type={'numeric'}
                value={step} onChange={(event) => setStep(Number(event.target.value))}/>

            }
            <Button onClick={onClickEdit}>
              <Create/>
            </Button>
            <DeleteButton/>
          </Box>
        </CardContent>

      </Card>

    </div>
  );
};
