import {AppBar, Box, Button, Toolbar} from "@mui/material";
import {styled} from "@mui/system";
import {JSXElementConstructor, ReactElement, ReactFragment, ReactPortal, useContext} from 'react';
import Moon from '../../infrastructure/ui/Moon';
import {Link} from 'react-router-dom';
import ExitToAppIcon from '@mui/icons-material/ExitToApp';
import {ItemsContext} from "../../infrastructure/ui/ItemsContext";
import {AuthContext} from "../../infrastructure/ui/AuthContext";

const StyledAppBar = styled(AppBar)({
  background: "#00C89D",
});

interface TopBarProps {
  title: string;
}

const TopBar = ({title}: TopBarProps) => {
  const {items, categories} = useContext(ItemsContext);
  const {userName, userMail, logout} = useContext(AuthContext);

  return (
    <StyledAppBar position="static">
      <Toolbar sx={{justifyContent: 'space-between'}}>
        {items.length > 0 &&
          <Moon/>
        }

        <Box sx={{flexGrow: 1, display: {xs: 'flex', md: 'flex'}, justifyContent: 'center'}}>
          {categories.map((tab: { name: string; icon: string | number | boolean | ReactElement<any, string | JSXElementConstructor<any>> | ReactFragment | ReactPortal | null | undefined; }) => (
            <Link
              to={'/home/' + tab.name}
              key={'tab' + tab.name}
            >
              <Button
                key={'tab' + tab.name}
              >
                {tab.icon}
              </Button>

            </Link>
          ))}
          <Link to={'/bot'} key={'tab' + 'bot'}>
            <Button key={'button-tab' + 'bot'}>
              🤖
            </Button>
          </Link>

        </Box>
        <Box sx={{textDecoration: 'none', color: 'white', '&:hover': {color: 'lightgray'}}}>
          {userMail}
          <Button onClick={logout}>
            <ExitToAppIcon style={{marginRight: '5px'}}/>
          </Button>
        </Box>

      </Toolbar>
    </StyledAppBar>
  );
};

export default TopBar;

