import {AppBar, Box, Button, Toolbar} from "@mui/material";
import {styled} from "@mui/system";
import React, {useContext, useEffect, useState} from 'react';
import {Link, useParams} from 'react-router-dom';
import {ItemsContext} from "../../infrastructure/ui/ItemsContext";
import {Room} from "../../domain/entities/room";

const StyledAppBar = styled(AppBar)({
  background: "#00FCBF",
  opacity: 0.6,
});

interface CategoryBarProps {
  category: string;
}

export const CategoryBarWrapper = () => {
  const {category} = useParams<{ category: string }>();
  return (
    <CategoryBar category={category || ''}/>
  );
}

export const CategoryBarWithRooms = ({
                                       roomsInCategory,
                                       onListClick,
                                       appBasePath = '/home',
                                     }: { roomsInCategory: Room[], onListClick: () => void, appBasePath: string }) => {

  return (
    <StyledAppBar position="static">
      <Toolbar sx={{justifyContent: 'space-between'}}>
        <Box sx={{flexGrow: 1, display: {xs: 'flex', md: 'flex'}, justifyContent: 'center'}}>
          {roomsInCategory.map((tab: { category: string; name: string; icon: string | number | boolean | React.ReactElement<any, string | React.JSXElementConstructor<any>> | React.ReactFragment | React.ReactPortal | null | undefined; }) => (
            <Link
              to={appBasePath + '/' + tab.category + '/' + tab.name}
              key={'tab' + tab.name}
            >
              <Button key={'tab' + tab.name}>
                {tab.icon}
              </Button>
            </Link>
          ))}

          <Button key={'tab_list'} onClick={onListClick}>
            📝
          </Button>
        </Box>

      </Toolbar>
    </StyledAppBar>
  );
}

const CategoryBar = ({category}: CategoryBarProps) => {
  const {items, categories, rooms} = useContext(ItemsContext);
  const [roomsInCategory, setRoomsInCategory] = useState(rooms.filter((room: { category: string; }) => room.category === category));

  const [itemsInCategory, setItemsInCategory] = useState(items.filter((item: { category: string; }) => item.category === category));

  const onListClick = () => {
    const groceryList = itemsInCategory
      .filter(item => item.quantity < item.max)
      .map(item => `${item.name}: ${item.max - item.quantity} ${item.unit ? item.unit : ''}`);

    alert(groceryList.join('\n'));
  };

  useEffect(() => {
    setRoomsInCategory(rooms.filter((room: { category: string; }) => room.category === category));
    setItemsInCategory(items.filter((item: { category: string; }) => item.category === category));
  }, [items, category, categories, rooms])


  return (
    <CategoryBarWithRooms roomsInCategory={roomsInCategory} onListClick={onListClick} appBasePath={'/home'}/>
  );
};
