import React from 'react';
import {useParams} from 'react-router-dom';
import Modeler from '../components/Modeler';
import Chatbot from '../components/ChatBot';
import {Box, Card} from '@mui/material';


export const BrainInterfaceWrapper = () => {
  const {brain} = useParams<{ brain: string }>();
  return (
    <BrainInterface brain={brain || ''}/>
  );
}


export const BrainInterface = ({brain}: { brain: string }) => {

  if (brain === 'model') {
    return (
      <div>
        <Modeler/>
      </div>
    )
  }
  if (brain === 'model') {
    return (
      <div>
        <Modeler/>
      </div>
    )
  } else if (brain === 'talk') {
    return (
      <div>
        <Chatbot/>
      </div>
    )
  }

  return (
    <div>
      <Box display="grid" flexDirection="column" alignItems="center">

        <Card>
          Brain not found
        </Card>
      </Box>
    </div>
  )

}

