import React, {createContext, ReactNode, useState} from 'react';
import {useMsal} from '@azure/msal-react';
import {loginRequest} from '../services/authConfig';

interface AuthContextProps {
  isAuthenticated: boolean;
  login: (username: string, password: string) => void;
  logout: () => void;
  userName: string;
  userMail?: string;
}

export const AuthContext = createContext<AuthContextProps>({
  isAuthenticated: false,
  userName: '',
  login: () => {
  },
  logout: () => {
  },
  userMail: ''
});

export const AuthProvider: React.FC<{ children: ReactNode }> = ({children}) => {
  const {instance} = useMsal();
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [userName, setUserName] = useState('');
  const [userMail, setUserMail] = useState('');

  const login = async (username: string, password: string) => {
    const authResult = await instance.loginPopup(loginRequest);
    if (authResult.account?.username) {
      setUserMail(authResult.account?.username);
    }
    if (authResult.account?.name) {
      setUserName(authResult.account?.name);
    }
    setIsAuthenticated(true);

  };

  const logout = async () => {
    // perform logout logic here
    // set isAuthenticated to false
    await instance.logout()
    setIsAuthenticated(false);
  };

  return (
    <AuthContext.Provider value={{isAuthenticated, login, logout, userName, userMail}}>
      {children}
    </AuthContext.Provider>
  );
};

export default AuthProvider;
